.background {
  width: 100vw;
  height: 100vh;
  overflow: none;
  display: block;
  background: url(background.svg) no-repeat;
  background-size: cover;
}
.animated-background{
	position: absolute;
	width: 100%;
	height: 100%;
}
.background.opening {
  animation-duration: 1s;
  animation-name: start-slide;
}
@keyframes start-slide {
  from {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}
.content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 19vh;
}
.content.opening {
  animation-name: fade;
  animation-duration: 2s;
}
.content.hidden {
  display: none;
}
.text {
  font-size: 1.1em;
  text-align: center;
  line-height: 1.3;
}
button.next {
  color: #c2c2c2;
  font-size: 150%;
  background-color: rgba(0, 0, 0, 0.6);
  border: 5px rgba(209, 209, 209, 0.8) solid;
  padding: 10px 50px;
}

button.next:hover {
  background-color: rgba(0, 0, 0, 0.9);
  border: 5px rgba(240, 240, 240, 1) solid;
  color: white;
  cursor: pointer;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.footer {
  margin-top: 10em;
}
.heading {
  font-size: 2.1em;
  padding: 20px;
}
.background.closing {
  animation-name: goOut;
  animation-duration: 4s;
  font-size: 21px;
}
.background.closing.right {
  animation-name: goOutRight;
  animation-duration: 3s;
  font-size: 21px;
}
@keyframes goOut {
  40% {
    margin-top: 50%;
    margin-left: -80vw;
    width: 10vw;
    height: 10vh;
    font-size: 10px;
  }
}
@keyframes goOutRight {
  50% {
    margin-top: 10%;
    margin-left: 140vw;
    width: 10vw;
    height: 10vh;
    font-size: 10px;
  }
}
a {
  text-decoration: none;
  cursor: pointer;
}
a:visited {
  color: inherit;
}
